import React from 'react'
import Item from './Item';

export default function Card(props) {
    const keys = Object.entries(props.contentObj).map(([key, value, index]) =>

        <>
            {
                key === "name" ? <h1 className={key} key={index}>{props.contentObj.name}</h1> :
                    key === "position" ? <h2 className={key} key={index}>{props.contentObj.position}</h2> :
                        key === "email" ? <a className={key} key={index} href={`mailto:${props.contentObj.email}`} >{props.contentObj.email}</a> :
                            key === "phone" ? <a className={key} key={index} href={`tel:${props.contentObj.phone}`}>{props.contentObj.phone}</a> :
                                key === "linkedin" ? <a className={key} key={index} href={props.contentObj.linkedin} target="_blank">{props.contentObj.linkedin.replace("https://www.", "")}</a> :
                                    key === "title" ? <h3 className={key} key={index}> {props.contentObj.title} </h3> :
                                        key === "description" ? <p>{props.contentObj.description}</p> :
                                            key === "image" ? <img alt={props.contentObj.title} src={require(`${props.contentObj.image}`)} /> :
                                                key === "skills" ? <p>{props.contentObj.skills.join(", ")}</p> :
                                                    Array.isArray(value) ? <Item itemsArr={value} /> :
                                                        <p>{value.toString()}</p>
            }
        </>

    );
    return (
        <div className="item">
            {keys}
            <hr />
        </div>
    )
}
