import React from 'react'


export default function Item(props) {

    const otems = props.itemsArr.map((itemObj) =>

        Object.entries(itemObj).map(([key, value, index]) =>
            <>
                {
                    key === "achievement" ? <li className={key} key={index}>{itemObj.achievement}</li> :
                        key === "stack" ? <ul><li>: {itemObj.stack}</li></ul> :
                            key === "language" ? <li className={key} key={index}>{itemObj.language}</li> :
                                key === "lavel" ? <ul><li>: {itemObj.lavel}</li></ul> :
                                    key === "company" ? <li className={key} key={index}>{itemObj.company}</li> :
                                        key === "industry" ? <ul><li>: {itemObj.industry}</li></ul> :
                                            key === "position" ? <ul><li>: {itemObj.position}</li></ul> :
                                                key === "institution" ? <li className={key} key={index}>{itemObj.institution}</li> :
                                                    key === "speciality" ? <ul><li>{itemObj.speciality}</li></ul> :
                                                        <li>{value.toString()}</li>
                }
            </>
        )
    )
    return (
        <ul className="itemItem">
            {otems}
        </ul>
    )
}
